<template>
    <div class="alphabet">
        <div class="box">
            <ul class="list">
                <li class="inem" v-for="(item,index) in cities" :key="index" @click="hendleLetter">{{index}}</li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    name:"CityAlphabet",
    props:{
        cities:Object
    },
    computed:{
        letters(){
            const letters = []
            for(let i in this.cities){
                letters.push(i)
            }
            return letters
        }
    },
    methods:{
        hendleLetter(e){
            var letter = e.target.innerText
            this.$emit("letterHandle",letter)
        }
    }
}
</script>
<style scoped>
    .alphabet{
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: fixed;
        right: 0px;
        bottom: 0px;
        top: 45px;
    }
    .list .inem{
        line-height: 20px;
        width: 20px;
        text-align: center;
        color: #00bcd4;
    }
    
</style>


