<template>
    <div class="rexiao">
            <h2 class="title">热销推荐</h2>
            <div class="count-list">
                <div class="count" v-for="item in list" :key="item.id">
                    <div class="left">
                        <img :src="item.imgUrl" alt="">
                    </div>
                    <div class="right">
                        <span class="right-title">{{item.title}}</span>
                        <span class="desc">{{item.desc}}</span>
                        <a href="" class="detitl">查看详情</a>
                    </div>
                </div>
            </div>
    </div>
</template>
<script>
export default {
    name:"ReXiao",
    props:{
        list:Array
    }
}
</script>
<style scoped>
    .title{
        height: 40px;
        background-color: #eee;
        line-height: 40px;
    }
    .count{
        display: flex;
        padding: 5px;
    }
    .left img{
        height: 100px;
        width: 100px;
    }
    .right{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-left: 15px;
    }
    .right-title{
        font-size: 16px;
    }
    .desc{
        color: #ccc;
    }
    .detitl{
        color: #fff;
        background-color: orange;
        width: 100px;
        text-align: center;
        padding: 10px 0;
        border-radius: 10px;
    }
</style>


