<template>
    <div class="locolist">
        <mt-swipe :auto="0">
            <mt-swipe-item v-for="(item,index) in pages" :key="index">
                <div class="loco" v-for="(loco,i) in item" :key="i">
                    <div class="loco-img">
                        <img :src="loco.imgUrl" alt="">
                    </div>
                    <div class="loco-text">
                        {{loco.desc}}
                    </div>
                </div>
            </mt-swipe-item>
        </mt-swipe>
    </div>
</template>
<script>
export default {
    name:"HomeLocoList",
    props:{
        list:Array
    },
    computed:{
        pages(){
            let pages = []
            this.list.forEach((item,index)=>{
                var page = Math.floor(index / 8)
                if(!pages[page]){
                    pages[page] = []
                }
                pages[page].push(item)
            })
            return pages
        }
    }
}
</script>
<style scoped>
    .locolist{
        height: 250px;
    }
    .loco{
        width: 25%;
        float: left;
        box-sizing: border-box;
        text-align: center;
    }
    .loco-img{
        padding: 10px;
    }
    .loco-img img{
        width: 100%;
    }
    .ioco-text{
        margin-top: 10px;
    }
</style>


