<template>
    <div class="wrapper" ref="wrapper" v-show="text">
        <div class="box">
            <div class="dq">
                <h2 class="title">当前城市</h2>
                <div class="button-list">
                    <div class="button-wrap">
                        <div class="button">北京</div>
                    </div>
                    <div class="button-wrap">
                        <div class="button">上海</div>
                    </div>
                    <div class="button-wrap">
                        <div class="button">广州</div>
                    </div>
                    <div class="button-wrap">
                        <div class="button">深圳</div>
                    </div>
                    <div class="button-wrap">
                        <div class="button">邯郸</div>
                    </div>
                </div>
            </div>
            <div class="hot">
                <h2 class="title">热门城市</h2>
                <div class="button-list">
                    <div class="button-wrap"  v-for="(item,index) in hot" :key="index">
                        <div class="button">{{item.name}}</div>
                    </div>
                </div>
            </div>
            <div class="list" v-for="(item,index) in cities" :key="index.id" :ref="index">
                <div class="title">{{index}}</div>
                <div class="button-wrap">
                    <div class="button" v-for="(item,index) in item" :key="index">{{item.name}}</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
    name:"CityList",
    mounted(){
        let wrapper = this.$refs.wrapper
        this.scroll = new BScroll(wrapper)
        
    },
    data(){
        return{
            text: true
        }
    },
    props:{
        cities:Object,
        hot:Array,
        letter:String,
        keyword:String
    },
    watch:{
        letter(){
            console.log("---")
            console.log(this.$refs[this.letter][0])
            this.scroll.scrollToElement(this.$refs[this.letter][0])
        },
        isShow(){
            console.log(this.keyword)
        }
    }
}
</script>
<style scoped>
    .box{
        overflow: hidden;
        position: absolute;
        right: 0;
        left: 0;
        z-index: -999;
    }
    .title{
        line-height: 30px;
        background-color: #eee;
    }
    .button-list{
        display: flex;
        flex-wrap: wrap;
    }
    .button-wrap{
        width: 33.33%;
    }
    .button{
        border: 1px solid #ccc;
        border-radius: 5px;
        box-sizing: border-box;
        text-align: center;
        padding: 5px 0;
        margin: 5px 15px;
    }
    .item{
        line-height: 35px;
        padding-left: 10px;
    }
</style>


