<template>
    <div class="city">
        <city-header></city-header>
        <city-serch :cities="cities"></city-serch>
        <city-list :cities="cities" :hot="hotCities" :letter="letter"></city-list>
        <city-alph :cities="cities" @letterHandle="receive"></city-alph>
    </div>
</template>
<script>
import axios from 'axios'
import CityHeader from './components/Header'
import CitySerch from './components/Serch'
import CityList from './components/List'
import CityAlph from './components/Alphabet'
export default {
    name:"City",
    components:{
        CityHeader,CitySerch,CityList,CityAlph
    },
    data(){
        return{
            cities:{},
            hotCities:[],
            letter:""
        }
    },
    mounted(){
        this.getCityInfo()
    },
    methods:{
        getCityInfo(){
            axios.get("/tour/static/api/city.json").then(this.handleCityInfo)
        },
        handleCityInfo(res){
            res = res.data
            console.log(res)
            if(res.data){
                const data = res.data
                this.cities = data.cities
                this.hotCities = data.hotCities
            }
        },
        receive(letter){
            console.log(letter)
            this.letter = letter
        }
    }
}
</script>

<style scoped>
    
</style>


