<template>
    <div class="box">
        <div class="search">
            <input type="text" class="input" v-model="keyword" placeholder="请输入城市名或拼音" :keyword="keyword">
        </div>
        <div class="search-content" ref="search" v-show="keyword">
            <ul>
                <li class="search-item" v-for="(item,index) in cityList" :key="index">
                    <div class="button">{{item}}</div>
                </li>
                <li class="search-item" v-show="noCity">没有找到匹配</li>
            </ul>
        </div>
    </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
    name:"CitySerch",
    props:{
        cities:Object
    },
    data(){
        return{
            keyword: '',
            cityList: [],
            timer: null,
            text:''
        }
    },
    computed: {
        noCity () {
            return !this.cityList.length
        }
    },
    watch: {
        keyword: function () {
            if (this.timer) {
                clearTimeout(this.timer)
            }
            this.timer = setTimeout(() => {
                if (!this.keyword) {
                    this.cityList = []
                    return
                }
                const result = []
                for (let i in this.cities) {
                    this.cities[i].forEach((value) => {
                        if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
                            result.push(value.name)
                        }
                })
                this.cityList = result
                }
            }, 100)
            console.log(this.cityList)
            
        },
        
    },
    mounted () {
        this.scroll = new BScroll(this.$refs.search)
    }
}
</script>
<style scoped>
    .box{
        background: #eee;
    }
    .search{
        height: 40px;
        background-color: #00bcd4;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .input{
        height: 30px;
        border: 1px solid #eee;
        border-radius: 5px;
        width: 90%;
        text-align: center;
        box-sizing: border-box;
    }
    .search-content{
        background-color: #eee;
        position: absolute;
        top: 1.58rem;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
    }
    .search-item{
        line-height: .62rem;
        padding-left: .2rem;
        color: #666;
        background: #fff;
    }
    .button{
        width: 50px;
        height: 50px;
        line-height: 50px;
        text-align: center;
    }
</style>


