<template>
    <div class="swipper">
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="(it,index) in list" :key="index">
                <a href="">
                    <img :src="it.imgUrl" alt="">
                </a>
            </mt-swipe-item>
        </mt-swipe>
    </div>
</template>
<script>
export default {
    name:"HomeSwipper",
    props:{
        list:Array
    }
}
</script>
<style scoped>
    .swipper{
        height: 200px;
    }
    .swipper img{
       width: 100%;
       height: 100%;
    }
</style>


