<template>
    <div class="home">
        <home-header></home-header>
        <home-swipper :list="sweList"></home-swipper>
        <home-loco :list="locoList"></home-loco>
        <home-re-xiao :list="rexiaolist"></home-re-xiao>
        <home-wekken :list="weeklist"></home-wekken>
    </div>
</template>
<script>
import axios from 'axios'
import HomeHeader from './components/Header'
import HomeSwipper from './components/Swipper'
import HomeLoco from './components/Loco'
import HomeReXiao from './components/ReXiao'
import HomeWekken from './components/Wekkend'
export default {
    name:"Home",
    components:{
        HomeHeader,HomeSwipper,HomeLoco,HomeReXiao,HomeWekken
    },
    data(){
        return {
            sweList:[],
            locoList:[],
            rexiaolist:[],
            weeklist:[]
        }
    },
    mounted(){
        this.getHomeDate()
    },
    methods:{
        getHomeDate(){
            axios.get("/tour/static/api/index.json")
            .then((res)=>{
                console.log(res.data.data)
                this.sweList = res.data.data.swiperList
                this.locoList = res.data.data.iconList
                this.rexiaolist = res.data.data.recommendList
                this.weeklist = res.data.data.weekendList
            })
        }
    }
}
</script>
<style scoped>

</style>


