<template>
    <div class="week">
        <h2 class="title">周末去哪</h2>
        <div class="week-list">
            <div class="count" v-for="item in list" :key="item.id">
                <img :src="item.imgUrl" alt="">
                <span class="title">{{item.title}}</span><br>
                <span class="desc">{{item.desc}}</span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:"WeekendList",
    props:{
        list:Array
    }
}
</script>
<style scoped>
    .week > .title{
        line-height: 40px;
        background-color: #eee;
    }
    .count img{
        width: 100%;
    }
    .count .title{
        font-size: 16px;
    }
    .desc{
        color: #ccc;
    }
    .count span{
        line-height: 25px;
    }
</style>


